.logo {
  width: 50px;
  height: 50px;
  position: fixed;
  z-index: 1000;
  top: $base-spacing-unit;
  left: $base-spacing-unit;
  transition: all $animation-speed $animation;
  opacity: 1;
  fill: $col-text-light;

  .light & {
    fill: $col-text;
  }

  .error &,
  .warning &,
  .success & {
    opacity: 0;
  }
}