.large-content {
  font-size: 18px;
}

.avatar {
  width: 100%;
  height: auto;
  border-radius: 50%;
  overflow: hidden;
  margin-bottom: $base-spacing-unit;

  img {
    width: 100%;
    height: auto;
    vertical-align: top;
  }
}

.avatar-caption {
  font-style: italic;
  font-size: 12px;
  text-align: center;
}

.section--latest-trip {
  min-height: 291px;
  padding: $base-spacing-unit 0;
  background-color: $col-background-dark;

  .trips {
    margin: 0;
  }
}

.section--photo-feed {
  margin-top: ($base-spacing-unit * 3);
}

.photo-feed {
  column-count: 2;
  column-gap: 0;
  max-width: 1200 + $base-spacing-unit;
  padding: 0 ($base-spacing-unit / 2);
  margin: 0 auto;

  @include resp-max($breakpoint-sm) {
    column-count: 1;
  }
}

.photo-feed__panel {
  break-inside: avoid;
  padding: $base-spacing-unit / 2;
  min-height: 200px; // to help lazyload
}

.see-more {
  margin-top: $base-spacing-unit;
  display: inline-block;
}