.flash {
  line-height: 1.35em;
  position: fixed;
  width: auto;
  z-index: 500;
  top: $base-spacing-unit;
  left: 0;
  font-weight: $font-weight-bold;
  padding: ($base-spacing-unit / 2) $base-spacing-unit 0;

  > p,
  > ul {
    margin-bottom: 0;
  }

  > p + p {
    margin-top: $base-spacing-unit;
  }

  .flash__content {
    position: relative;
    display: inline-block;
    padding-right: $base-spacing-unit * 2;
  }

  .flash__dismiss {
    color: $col-text-light;
    font-size: 32px;
    text-decoration: none;
    font-weight: normal;
    position: absolute;
    right: 0;
    top: 0;
    padding: 0;
    vertical-align: top;
    line-height: .5;

    &:hover {
      text-decoration: none;
    }

    .light & {
      color: $col-text;
    }
  }

  ul {
    margin: 0;
  }
}