@mixin responsive-photo {
  display: block;
  margin-bottom: $base-spacing-unit;
  @include clearfix();

  .photo__image-container {
    // not the best, but sometimes we have to go easier route.
    transform: translate(0, 0) !important;
    height: auto !important;
    margin-bottom: $base-spacing-unit;
  }

  .photo__caption {
    // not the best, but sometimes we have to go easier route.
    transform: translate(0, 0) !important;
    display: inline-block;
  }

  img {
    height: auto !important;
  }
}

.trip-container {
  width: $grid-max-width + 150; // grid + offsetX range for photo
  max-width: 100%;
  margin: 0 auto;
  overflow: hidden;
}
.photos {
  margin-top: $base-spacing-unit * 3;

  img {
    max-width: 100%;
    height: auto;
  }
}

.photo {
  position: relative;
  display: flex;
}

.photo__image-preview {
  position: absolute;
  filter: blur(20px);
  z-index: 1;
  top: 0;
  left: 0;
}

.photo__image {
  position: relative;
  z-index: 5;
}

.photo__caption {
  align-self: center;
  font-family: $serif-font-family;
  font-weight: $serif-font-weight;
}

.photo--odd {
  flex-direction: row-reverse;
  text-align: right;

  .photo__caption {
    right: inherit;
    left: 0;
  }
}

.photo--landscape {
  @include resp-max($breakpoint-lg) {
    @include responsive-photo();
  }
}

.photo--square {
  @include resp-max($breakpoint-md) {
    @include responsive-photo();
  }
}

.photo--portrait {
  @include resp-max($breakpoint-sm) {
    @include responsive-photo();
  }
}

.photo__image-container {
  position: relative;
  display: inline-block;
  background: $col-background-dark;
  background: linear-gradient(135deg, $col-background-dark 0%, $col-brand 100%);
  z-index: 20;
  max-width: 100%;
  overflow: hidden;

  &:hover {
    .photo__info {
      opacity: 1;
      visibility: visible;
    }
  }
}

.photo__info {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  display: flex;
  right: 0;
  left: 0;
  height: 25%;
  bottom: 0;
  z-index: 25;
  font-size: 14px;
  color: $col-text-light;
  background: linear-gradient(0deg, $col-brand 15%, transparent 100%);
  transition: all $animation-speed linear;

  .photo__info__content {
    align-self: flex-end;
    padding: ($base-spacing-unit / 2) 0;
  }
}

.photo__info__icon {
  fill: $col-text-light;
  width: 20px;
  height: 20px;
  display: inline-block;
  vertical-align: middle;
}

.list--photo-info {
  list-style: none;
  margin: 0;
  padding: 0;

  li {
    display: inline-block;
    margin: 0 ($base-spacing-unit / 2);
    line-height: 20px;
  }
}

.link {
  &.link--all-trips {
    margin-top: $base-spacing-unit * 5;
    display: inline-block;
  }
}