.code-feed {
  .col {
    margin-bottom: $base-spacing-unit;
  }
}

.code {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: $col-background;
  padding: ($base-spacing-unit / 2) $base-spacing-unit;
  color: $col-text;
}

.code__content {
  flex: 1 0 auto;
}

.code__link {
  margin-right: $base-spacing-unit;
  display: inline-block;
}