.trip-editor {
  margin-bottom: $base-spacing-unit * 2;
}

.photo-thumbs {
  text-align: left;
  margin-bottom: $base-spacing-unit;
}

.photo-thumb {
  display: inline-block;
  padding: 2px;
  cursor: move;
  background-color: $col-neutral-lightest;
}

.gu-mirror {
  position: fixed;
  margin: 0;
  z-index: 9999;
  opacity: 0.8;
  filter: alpha(opacity = 80);
}

.gu-hide {
  display: none;
}

.gu-unselectable {
  user-select: none;
}

.gu-transit {
  opacity: 0.2;
  filter: alpha(opacity = 20);
}