.record {
  margin-bottom: $base-spacing-unit;
}

.record__artwork {
  cursor: pointer;
  width: 100%;
  padding-bottom: 100%;
  background: lighten($col-background-dark, 10%);
  margin-bottom: $base-spacing-unit / 4;
  position: relative;
  margin-bottom: $base-spacing-unit;

  &:before {
    content: '';
    width: 75%;
    height: 75%;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    border: 2px solid $col-background-dark;
    transform: translate(-50%, -50%);
  }

  &:after {
    content: '';
    width: 20%;
    height: 20%;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    background-color: $col-background-dark;
    transform: translate(-50%, -50%);
  }

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 100%;
    height: auto;
    z-index: 5;
  }
}

.record__title {
  font-size: 14px;
}

.modal--release {
  .modal-container {
    width: 70%;

    @include resp-max($breakpoint-sm) {
      width: 100%;
    }
  }
}

.record__link_icon {
  display: inline-block;
  margin-bottom: $base-spacing-unit;
  width: 100%;

  .icon {
    display: inline-block;
    vertical-align: middle;
    width: 40px;
    height: 40px;
    margin-right: $base-spacing-unit;
  }
}

.record__link_caption {
  display: inline-block;
  vertical-align: middle;
}

.record-wrapper {
  @include resp-max(525px) {
    flex-basis: 50%;
    max-width: 50%;
  }
}

.list__track_title {
  // display: inline-block;
  // vertical-align: middle;
}

.list__track_play_pause {
  width: 20px;
  height: 20px;
  display: inline-block;
  vertical-align: middle;
  margin-right: $base-spacing-unit / 4;
  cursor: pointer;

  .icon {
    width: 20px;
    height: 20px;
  }
}