@import '../../../node_modules/medium-editor/src/sass/medium-editor';
@import '../../../node_modules/medium-editor/src/sass/themes/beagle';

.editor {
  position: relative;
}

.editable {
  outline: 0 none;
}

.editor__content {
  font-family: $serif-font-family;
  font-weight: $serif-font-weight;
}

.media-button {
  width: 26px;
  height: 26px;
  border-radius: 50%;
  position: absolute;
  left: -($base-spacing-unit + 26);
  bottom: 67px;
}

.media-button__icon {
  width: 26px;
  height: 26px;
  fill: $col-text;
}